import PropTypes from 'prop-types';
// @mui
import { styled } from '@mui/material/styles';
import { Box, Avatar, Typography } from '@mui/material';
import _mock from '../../_mock';
// components

// ----------------------------------------------------------------------

const TopLeftStyle = styled('div')(({ theme }) => ({
  top: 0,
  left: 0,
  zIndex: 10,
  position: 'absolute',
  paddingLeft: theme.spacing(3),
  paddingTop: theme.spacing(3),
}));

const video = {
  light: _mock.image.product(7),
  video: "https://media.w3.org/2010/05/sintel/trailer_hd.mp4"
}
// ----------------------------------------------------------------------



export default function VideoDisplay() {

  const channelAvatar = _mock.image.product(7);
  const channelTitle = _mock.text.title(9);
  const channelSubtitle = _mock.name.fullName(6);

  return (

    <Box sx={{ width: "100%", maxHeight: "85vh" ,height:"auto", position: 'relative',backgroundColor:'black' }}>
      <video controls muted
      width="100%" height="100%"
      poster = {video.light}
      >
        <source src={video.video} type="video/mp4" />
      </video>
      <TopLeftStyle>
        <Box sx={{ display: 'flex', alignItems: 'center' }}>
          <Avatar alt={channelTitle} src={channelAvatar} sx={{ width: 60, height: 60 }} />
          <Box sx={{ ml: 2 }}>
            <Typography variant="subtitle1" sx={{ color: 'common.white' }}>
              {channelTitle}
            </Typography>
            <Typography variant="body2" sx={{ color: 'grey.500' }}>
              {channelSubtitle}
            </Typography>
          </Box>
        </Box>
      </TopLeftStyle>
    </Box>




  );
}
