import * as React from 'react';
import { Stack, ImageList, ImageListItem, ImageListItemBar, IconButton } from '@mui/material'
import Image from '../../components/Image';
import Iconify from '../../components/Iconify';

import _mock from '../../_mock'

const itemData = [...Array(15)].map((_, index) => ({
  productImage: _mock.image.product(index),
  productName: _mock.name.firstName(index),
  productId: _mock.id(index)
}));

export default function CustomImageList() {
  return (
    <Stack spacing={3} sx={{ px: 2}}>
      <ImageList
        sx={{ width: "100%", transform: 'translateZ(0)', }}
        gap={10}
      >
        {itemData.map((item) => {
          return (
            <ImageListItem key={item.productId} sx={{ borderRadius: 1, overflow: "hidden" }}>
              <Image
                src={item.productImage}
                alt={item.productName}
                ratio="4/3"
              />
              <ImageListItemBar
                sx={{
                  background:
                    'linear-gradient(to bottom, rgba(0,0,0,0.7) 0%, ' +
                    'rgba(0,0,0,0.3) 70%, rgba(0,0,0,0) 100%)',
                }}
                title={item.productName}
                position="top"
                actionIcon={
                  <IconButton
                    sx={{ color: 'white' }}
                    aria-label={item.productName}
                  >
                    <Iconify icon="eva:eye-outline" width={25} height={25} />
                  </IconButton>
                }
                actionPosition="left"
              />
            </ImageListItem>
          );
        })}
      </ImageList>
    </Stack>
  );
}
